
.main_container{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: white;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
.heading{
    font-size: 1.6rem;
    color: rgb(48, 48, 48);
    font-weight: 700;
    background-color: rgb(141, 5, 141);
    padding: 1rem;
    color: white;
    /* text-decoration: underline; */
}
.active{
    text-decoration: line-through;
}
form{
    display: flex;
    width: 100%;
    margin-bottom: 14px;
}
form input{
    height: 40px;
    width: calc(100% - 20%);
    border-radius: 8px 0px 0px 8px;
    border-right: none;
    padding: 0 13px;
    font-size: 1rem;
}
form button{
    width: 20%;
    border-radius: 0px 8px 8px 0px;
    border-left: none;
    background-color: rgb(180, 16, 180);
    color: white;
    font-weight: 700;
    cursor: pointer;
}
.todoitem{
    display: flex;
    width: 100%;
    justify-content: space-between;
    background-color: aliceblue;
    /* margin-top: 2px; */
    /* padding-left: 0.3rem; */
    /* padding-right: 0.3rem; */
    /* border-radius: 10px; */
    font-family: Poppins;
    font-size: 1.2rem;
    font-weight: 700;
    padding-top: 1rem;
    padding-bottom: 1rem;
    border: 1px solid grey;
}
.todolist{
    /* padding: 1px; */
    height: 350px;
    overflow-y: scroll;
    border: 2px solid lightgrey;
    border-radius: 10px;
    /* scroll-behavior: smooth; */
    /* overflow-x: scroll; */
}
.buttons button{
    background-color: transparent;
    /* color: transparent; */
    border: none;
    margin: 5px;
    cursor: pointer;
}
.main_section{
    width: 90%;
}
@media screen and (min-width: 780px) {
    .main_section {
        width: 400px;
    }
}